    .images__holder {
        min-height: 0px;
    }
    .main{
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr 3fr 1fr;
        grid-template-areas: "logo"
                             "questImg"
                             "menu"
                             "shortDesc"
                             "longDesc"
                             "buttons";
        min-height: 95vh;
    }
    .question__image{
        min-height: 300px;
